.button {
  box-sizing: border-box;
  color: #f1f5f4;
  background-color: #cd4d31;
  justify-content: center;
  align-items: center;
  height: 60px;
  text-decoration: none;
  transition: transform .3s;
  display: flex;
}

.button:hover {
  transform-origin: center;
  transform: scale(1.2);
}

@media (width <= 767px) {
  .button {
    width: 280px;
  }
}

@media (width >= 768px) and (width <= 1279px) {
  .button {
    width: 330px;
  }
}

@media (width >= 1280px) {
  .button {
    width: 270px;
  }
}

.button__title {
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
  width: 210px;
  height: 60px;
  transition: transform .3s;
  display: flex;
}

@media (width <= 767px) {
  .button__title {
    display: none;
  }
}

.button__title:hover {
  transform-origin: center;
  transform: scale(1.2);
}

.button--white {
  background-color: #f1f5f4;
  border: 1px solid #cd4d31;
}

.button__text {
  text-align: center;
  color: #f1f5f4;
  justify-content: center;
  align-items: center;
  font-family: Raleway, sans-serif;
  font-size: 16px;
  font-weight: 600;
  line-height: 16px;
  display: flex;
}

.button__text--main {
  text-align: center;
  color: #cd4d31;
  font-family: Raleway, sans-serif;
  font-size: 16px;
  font-weight: 600;
  line-height: 16px;
}

.button--arrow {
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
  margin-left: 8px;
  display: flex;
}

.button--arrow img {
  width: 100%;
}

.button--submit {
  cursor: pointer;
  border: none;
}

